import React from 'react';
import { render } from 'react-dom';
import { AuthenticationFailed, AuthError } from '@chr/web-components-labs';
import { EnsureAuthedError } from './index';

export const handleAuthError = (error: EnsureAuthedError) => {
  render(<AuthenticationFailed error={{error:error.error} as AuthError} />,
    document.getElementById('root')
  );
}